'use strict';
import LjlColumn from './components/LjlColumn.vue';
import { RIGHT } from '@/config/image.js';

export default {
	data() {
		return {
			img: {
				RIGHT
			}
		}
	},
	props: {
		header: {
			type: Boolean,
			default() {
				return true;
			}
		},
		list: {
			type: Array,
			default() {
				return [];
			}
		}
	},
	methods: {
		handChilck(e) {
			let index = e.target.dataset.index || 0;
			this.$emit("change", this.list[index].state);
		}
	},
	components: {
		LjlColumn
	}
};
